<template>
  <video-player  class="vjs-custom-skin"
                 ref="videoPlayer"
                 @pause="onPlayerPause($event)"
                 :options="playerOptions"
                 :playsinline="true">
  </video-player>
</template>

<script>
import { mapState, mapMutations } from 'vuex'
import 'video.js/dist/video-js.css'
import { videoPlayer } from 'vue-video-player'
export default {
  name: 'VueVideoPlayer',
  data () {
    return {
      playerOptions: {
        playbackRates: [0.7, 1.0, 1.5, 2.0], // 
        autoplay: true, // true,
        muted: false, // 
        loop: true, // 
        preload: 'auto', // <video>auto,
        language: 'zh-CN',
        aspectRatio: '16:9', //  - "16:9""4:3"
        fluid: true, // trueVideo.js player
        sources: [{
          type: 'video/mp4', // git
          src: this.MVUrl // url
        }],
        poster: '', // 
        // width: document.documentElement.clientWidth, //
        notSupportedMessage: '', // Video.js
        controlBar: {
          timeDivider: true,
          durationDisplay: true,
          remainingTimeDisplay: true,
          fullscreenToggle: true // 
        }
      },
      MVUrl: ''
    }
  },
  components: {
    videoPlayer
  },
  computed: {
    ...mapState(['MVid', 'flag']),
    player () {
      return this.$refs.videoPlayer.player
    }
  },
  created () {
    this.getMVUrl()
  },
  watch: {
    '$store.state.MVid': function (val) {
      console.log(val)
      this.getMVUrl()
    },
    '$store.state.flag': function (val) {
      console.log(val)
      if (val === 1) {
        this.PlayerPlay()
      } else {
        this.PlayerPause()
      }
    }
  },
  methods: {
    ...mapMutations(['setFlag']),
    async getMVUrl () {
      const result = await this.axios.get('/mv/url', {
        params: {
          id: this.MVid
        }
      })
      console.log(result)
      this.MVUrl = result.data.data.url
      this.playerOptions.sources[0].src = this.MVUrl
    },
    PlayerPlay () {
      this.$refs.videoPlayer.player.play()
    },
    PlayerPause () {
      this.$refs.videoPlayer.player.pause()
    },
    onPlayerPause (player) {
      console.log('player pause!', player)
      this.setFlag(-1)
    }
  }
}
</script>

